	module.exports = {"data":{"markdownRemark":{"html":"<p>There’s a lot that I want to become, and its overwhelming when I try to conceive of what it would take to fulfill my most ambitious visions of myself.</p>\n<p>One good trick I’ve found that works for me personally is writing it all down. Once the words have been captured by paper and ink they seem more manageable, more well-behaved.</p>\n<p>This almost automatically leads to doing a little bit every day. Or most days. Be micro-ambitious.</p>\n<p>Which reminds me of my third helpful tactic: embracing imperfection. If I’m making good choices <em>most</em> of the time, even just 51%, I’m doing just fine. This helps me avoid black and white thinking about myself: bad choices leading to thinking of myself as “bad”, which makes me more prone to more bad choices. Or, good choices leading to me thinking I deserve to make some bad choices. A relevant reflection: humans are the only animals that can suffer for things not happening now.</p>\n<blockquote>\n<p>If you are depressed you are living in the past. If you are anxious you are living in the future. If you are at peace you are living in the present.</p>\n</blockquote>\n<p>― Lao Tzu</p>\n<p>On the flip side, we can also be happy about things not happening right now. I’m not sure how to synthesize that with the above, but I feel it’s an important point to note.</p>\n<p>The fourth practice is getting very, very clear about just what “good” is. Aristotle believed that truly knowing what is good necessarily leads to good action. While he might have overstated his case, I’ve found that the more clear I am about what I need to do, the easier it is to do it. One thing I’m that clear on is the importance of being well-rounded, not being pigeon-holed. I’m not sure why I want that so much, I just know I get really bored when there isn’t much change in my day-to-day work.</p>\n<p>My primary occupation these days is software engineer and co-founder of a startup, but I’m also interested in:</p>\n<ul>\n<li>writing [active]</li>\n<li>drawing [active]</li>\n<li>mindfulness [active]</li>\n<li>physical fitness [active]</li>\n<li>learning to be more musical [inactive]</li>\n<li>overcoming stage fright / shyness [inactive]</li>\n<li>political activism [inactive]</li>\n</ul>\n<p>For the ones marked [active] I’ve been doing a little bit, most days, that is 51%. The political activism one really bothers me. I see so much happening that I feel is wrong, but because there’s so much happening relative to how much time I have to give, I get discouraged. The same principle of doing a little every day should apply, but for some reason it seems harder in this case. Perhaps because it’s a rabbit hole, and one I’ve been down before. But if something you love is being threatened, is there any justification for not doing <em>something</em>? Anyway, I hope by the end of 2018 I can consider myself active in those last three areas, especially the last.</p>","excerpt":"There’s a lot that I want to become, and its overwhelming when I try to conceive of what it would take to fulfill my most ambitious visions of myself. One good trick I’ve found that works for me personally is writing it all down. Once the words have…","frontmatter":{"date":"December 28, 2017","path":"/blog/2017/12/28/new-years-2017","title":"On the being and nothingness of Patrick / end of 2017 reflections","category":"life, the universe, and everything"}}},"pathContext":{}}

